export function getPmFormat(pm) {
  if (!pm) return;

  if (pm === "1") return "";
  else if (pm === "2") return "";
  else if (pm === "3") return "";
  else if (pm === "4") return " ";
  else return;
}

export function getUvFormat(uv) {
  if (!uv) return;

  uv = Number(uv);

  if (uv < 3) return "";
  else if (3 <= uv && uv < 6) return "";
  else if (6 <= uv && uv < 8) return "";
  else if (8 <= uv && uv < 11) return " ";
  else if (11 <= uv) return "";
  else return;
}

export function getWindFormat(wind) {
  if (!wind) return;

  wind = Number(wind);

  if (wind < 4) return "";
  else if (4 <= wind && wind < 9) return " ";
  else if (9 <= wind && wind < 14) return "";
  else if (14 <= wind) return " ";
  else return;
}

export function changeColor(value) {
  if (!value) return;

  if (value === "1" || value === "" || value === "" || value === "")
    return "text-green";
  else if (value === "2" || value === " " || value === "")
    return "text-yellow";
  else if (
    value === "3" ||
    value === "4" ||
    value === "" ||
    value === " " ||
    value === " " ||
    value === ""
  )
    return "text-red"; // 3(-), 4( -), (),  (),  (), ()
  else return;
}

export function pmTimeFormat(date) {
  if (!date) return;

  const word = date.split(" ");
  const hour = Number(word[1].slice(0, 2));

  return hour;
}

export function sunsTimeFormat(time) {
  if (!time) return;

  time = time.toString();

  const hour = time.slice(0, 2);
  const minute = time.slice(2, time.length);
  return `${hour < 12 ? "" : ""} ${hour}:${minute}`;
}

export function timeFormat(type, date) {
  if (!date) return;

  let time = "";

  if (type === "pm") {
    const word = date.split(" ");
    const hour = Number(word[1].slice(0, 2));
    time = `${hour} `;
    //
  } else if (type === "uv") {
    const hour = Number(date.slice(date?.length - 2));
    time = `${hour} `;
    //
  } else {
    const hour = Number(date.slice(0, 2));
    const minute = Number(date.slice(2));

    time = `${hour} ${minute > 0 ? minute + " " : ""}`;
  }

  return time;
}

export function compoundCardData(card, uv, air, sun) {
  const updateTime = timeFormat("", card?.fcstTime);

  const data = [
    {
      type: air && air.pm10Grade1h !== "" ? 1 : 0,
      id: "pm",
      name: "",
      title: " ",
      titleIcon: false,
      text1: "",
      value1: getPmFormat(air?.pm10Grade1h),
      text2: "",
      value2: getPmFormat(air?.pm25Grade1h),
      color1: changeColor(air?.pm10Grade1h),
      color2: changeColor(air?.pm25Grade1h),
      stationName: air?.stationName,
      time: timeFormat("pm", air?.dataTime),
    },
    {
      type: uv ? 2 : 0,
      id: "uv",
      name: "",
      title: " ",
      titleIcon: false,
      text1: getUvFormat(uv?.h0),
      value1: `${uv?.h0}mm`,
      color1: changeColor(getUvFormat(uv?.h0)),
      time: timeFormat("uv", uv?.time),
    },
    {
      type: card ? 2 : 0,
      id: "wind",
      name: "",
      title: " ",
      titleIcon: true,
      text1: getWindFormat(card?.wsd),
      value1: `${card?.vec} - ${card?.wsd}m/s`,
      color1: changeColor(getWindFormat(card?.wsd)),
      time: updateTime,
    },
    {
      type: card ? 3 : 0,
      id: "humidity",
      name: "",
      title: " ",
      titleIcon: true,
      value1: `${card?.reh}%`,
      time: updateTime,
    },
    {
      type: card ? 3 : 0,
      id: "precipitation",
      name: "",
      title: " ",
      titleIcon: true,
      value1: card?.rn1,
      time: updateTime,
    },
    {
      type: sun ? 1 : 0,
      id: "sun",
      name: "",
      title: " /",
      titleIcon: true,
      text1: "",
      value1: sunsTimeFormat(sun?.sunrise),
      text2: "",
      value2: sunsTimeFormat(sun?.sunset),
    },
  ];

  return data;
}
