import * as dateUtil from "./dateUtil";

function sentenceSort(data) {
  const sortData = [];

  data.time?.forEach((hour, index) => {
    const arr = [
      hour === "3 " ? -3 : Number(hour) / 100,
      Number(data.date[index]),
      data.message[index],
    ];
    sortData.push(arr);
  });

  sortData.sort((a, b) => {
    if (a[0] > b[0]) {
      if (a[1] > b[1]) return 1;
      else if (a[1] < b[1]) return -1;
      else return 1;
    } else if (a[0] < b[0]) {
      if (a[1] < b[1]) return -1;
      else if (a[1] > b[1]) return 1;
      else return -1;
    } else {
      if (a[1] < b[1]) return -1;
      else if (a[1] > b[1]) return 1;
      else return 0;
    }
  });

  return sortData;
}

function dupleDataSort(data) {
  const resultArr = [];
  let message = "";
  data
    .filter((value) => value[0] === -3)
    .forEach((item, index, arr) => {
      //
      if (index < arr.length - 1) {
        message += `${item[2]}`;
      } else {
        message += item[2];
        resultArr.push([item[0], item[1], message]);
      }
    });

  const passIdxArr = [];
  data
    .filter((value) => value[0] !== -3)
    .forEach((item, index, arr) => {
      const length = arr.length - 1;

      if (length === 0) {
        resultArr.push(item);
        return;
      }

      if (passIdxArr.indexOf(index) > -1) return;
      resultArr.push(item);

      for (let i = index + 1; i <= length; i++) {
        if (item[2] === arr[i][2]) {
          passIdxArr.push(i);
        } else {
          break;
        }
      }
    });

  return resultArr;
}

// Main.jsx - One sentence card format
export function sentenceFormat(data) {
  if (!data) return;

  const currentDate = dateUtil.getDate(null, "YYMMDD");

  const sortData = sentenceSort(data);
  const dupleData = dupleDataSort(sortData);
  // console.log(data);
  // console.log(sortData);
  // console.log(resultData);

  let result = "";
  dupleData.forEach((item, index) => {
    if (item[0] === -3) {
      result += "3  ";
    } else {
      if (currentDate < item[1]) result += " ";
      result += `${dateUtil.TimeFormat(item[0])} `;
    }

    result += item[2];
    result += index < dupleData.length - 1 ? ", " : "  .";
  });

  return result;
}

